@font-face {
  font-family: croogla;
  src: url(./assets/croogla.woff2);
}

* {
  text-align: center;
  font-family: croogla;
}
#logo {
  max-width: 100%;
  height: auto;
}
body {
  background-repeat: no-repeat;
  background-size:cover;
}

.parent {
  height: 100vh;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 25% 45% repeat(2, 15%);
  grid-column-gap: 0px;
  grid-row-gap: 0px;
}

.div1 { grid-area: 1 / 1 / 2 / 2; }
.div2 { grid-area: 2 / 1 / 3 / 2; }
.div3 { grid-area: 3 / 1 / 4 / 2; }
.div4 { grid-area: 4 / 1 / 5 / 2; }

#soon {
  position: relative;
  margin: 0 auto;
  margin-top: 8vh;
  
}

.blue {
  color: #003CEF;
}

.tagline {
  margin-top: 0px;
  font-size: 48px;
}

.follow {
  margin-bottom: 0px;
}

.url {
  margin-top: 0px;
}

.custom_input {
  width: 400px;
display: flex;
align-items: center;
position: relative;
max-width: 50%;
  margin-left: 123px;
}

.input {
font-size: 24px;
  text-align:left;
padding: 5px 10px;
width: 100%;
  height: 50px;
padding-right: 10px;
outline: none;
background: #FFFFFF;
color: #000000;
border: 6px solid rgba(0, 60, 239, 0.3);
border-radius: 18px;
box-shadow: 3px 3px 2px 0px #E2E2E2;
transition: .3s ease;
}

.input:focus {
background: #F2F2F2;
border: 6px solid rgb(0, 60, 239);
border-radius: 18px;
}

.input::placeholder {
color: #adadad;
}

.svg_icon {
position: absolute;
right: 35px;
fill: #4660DC;
width: 18px;
height: 18px;
}

.submitButon {
box-shadow: 0px 4px 0px 0px #000000;
background-color:#0004ff;
border-radius:28px;
display:inline-block;
cursor:pointer;
color:#ffffff;
font-family:Arial;
font-size:19px;
padding:16px 28px;
text-decoration:none;
text-shadow:0px 1px 0px #2f6627;
}
.submitButon:hover {
background-color:#0004e8;
}
.submitButon:active {
position:relative;
top:1px;
}

.custom_button {
  width: 128px;
  height: auto;
  margin-left: 10px;
}

.container-input {
  display: flex;
  align-items: center;
  justify-content: center;
      
}

.thumb {
  position:absolute;
  top: 50%;
  right: 0%;
  transform: translate(0%, -50%);
}

@media screen and (max-width: 1575px) {
  .thumb {
      display: none;
  }
  #logo {
      height: 175px;
  }
}
@media screen and (max-width: 768px) {
.parent {
  grid-template-rows: 20% 50% repeat(2, 15%);
}

.follow .url {
  font-size: 10px;
}

#soon {
  margin-top: 4vh;
  width: 200px;
}

.tagline {
  font-size: 36px;
}

.custom_input {
  max-width: 80%;
  margin-left: 0px;
}

.input {
  font-size: 18px;
}

.svg_icon {
  right: 20px;
}

.custom_button {
  margin-left: 0px;
  margin-top: 10px;
}

.container-input {
  flex-direction: column;
}
#logo {
  height: 85px;
}
}

@media screen and (max-width: 480px) {
  .parent {
      grid-template-rows: 5% 50% repeat(2, 15%);
  }
  body {
      overflow:hidden;
  }
  #soon {
      margin-top: 4vh;
      width: 200px;
  }
  
  .tagline {
      font-size: 15px;
  }
  .follow .url {
      font-size: 13px;
  }
  #subtext {
      font-size: 12px;
  }
  .custom_input {
      max-width: 92%;
  }
  .custom_button {
      margin-left: 0%;
      margin-top: 3%;
      margin-right: -2%;
  }
  .input {
      font-size: 16px;
  }
  .svg_icon {
      right: 15px;
  }
  
  .submitButon {
      font-size: 16px;
      padding: 12px 24px;
  }
  #logo {
      height: 65px;
      padding-top: 50px;
  }

  #tr {
      padding-right: 0% !important;
  }
}
@media screen and (max-width: 384px) {
  .parent {
      grid-template-rows: 5% 50% repeat(2, 15%);
      margin-right: 7.5%;
  }
  #soon {
      margin-top: 4vh;
      width: 200px;
      padding-right: 0% !important;
      margin-right: 7.5%;
  }
  
  .tagline {
      font-size: 15px;
      padding-right: 0% !important;
      margin-right: 7.5%;
  }
  #subtext {
      font-size: 12px;
      padding-right: 0% !important;
      margin-right: 7.5%;
  }
  .custom_input {
      max-width: 80%;
      padding-right: 0% !important;
      margin-right: 8%;
      margin-top: 5%;
      
  }
  .custom_button {
      margin-left: 0%;
      margin-top: 3%;
      margin-right: 12%;
  }
  .input {
      font-size: 16px;
      padding-right: 0% !important;
  }
  .svg_icon {
      right: 15px;
      padding-right: 0% !important;
      margin-right: 7.5%;
  }
  
  .submitButon {
      font-size: 16px;
      margin-top: 12px;
      margin-left: 13.5%;
  }
  #logo {
      height: 65px;
      padding-top: 50px;
      padding-right: 0% !important;
      margin-right: 7.5%;
  }
  #c1 #c2 #c3 #c4 {
      margin-right: 6%;
  }
  #tr {
      padding-right: 0% !important;
  }
  #as {
      margin-right: 7.5%;
      font-size: 22px;
  }
  #devup {
      margin-right: 7.5%;
      font-size: 18px;
  }
}